export default {
    data() {      
        return {
            url:"",
            deviceNum : 3,
            deviceList: [],
			deviceId:""
        }
    },
    methods: {
        /*
         * openlimit
         * 打开限制，调用接口，post传递数据
         */ 
        openlimit : function(device) {
			console.log(device);
            device.flag = 1;
            uni.request({
                url: this.url+'web/api/updateSwitchFlag',
                data: {deviceUserId: device.deviceUserId,flag: 1,},
                header: {
                    'content-type': 'application/json'
                },
                method: 'POST', // OPTIONS, GET, HEAD, POST, PUT, DELETE, TRACE, CONNECT
                // header: {}, // 设置请求的 header
                success: function(res){
                    uni.showToast({
                        title:"开启成功",
                        icon: 'success',
                        duration: 1500
                       })
                },
                fail: function() {
                    // fail
                },
                complete: function() {
                    // complete
                }
            });
        },
        /*
         * closelimit
         * 关闭限制，调用接口，post传递数据
         */ 
        closelimit : function(device) {
            device.flag = 0;
            console.log(device.deviceUserId);
            uni.request({
                url: this.url+'web/api/updateSwitchFlag',
                data: {deviceUserId:   device.deviceUserId,
                        flag: 0,},
                header: {
                    'content-type': 'application/json'
                },
                method: 'POST', 
                success: function(res){
                    console.log(res);
					uni.showToast({
					    title:"关闭成功",
					    icon: 'success',
					    duration: 1500
					   })
                },
                fail: function() {
                    // fail
                },
                complete: function() {
                    // complete
                }
            });
        },
        add : function() {
            const url='./addDevice';
            uni.navigateTo({ url });
        },
        edit : function() {
            const url='./editDevice';
            uni.navigateTo({ url });
        },
    },

    onShow: function() {
        this.url=this.$consturl;
        let that = this;
        that.deviceId=uni.getStorageSync('deviceId');
        uni.request({
            url: that.url+'web/api/getDeviceUserList',
            data: {deviceId:that.deviceId},
            header: {
                'content-type': 'application/json'
            },
            method: 'POST', 
            success: function(res){

                console.log(res.data);
                that._data.deviceList = res.data;
            },
            fail: function() {
                // fail
            }

        });
    },
}
